export default {
  data: function data() {
    return {
      isMobile: false
    };
  },
  computed: {
    loading: function loading() {
      return this.$store.getters.loading;
    }
  },
  created: function created() {
    var vm = this;
    vm.$nextTick(function () {
      var search = window.location.search.replace('?id=', '');
      var params = search.split("-secretKey=");

      if (params.length === 2) {
        var dossierNo = params[0];
        var secretKey = params[1];
        var url = window.location.origin + window.location.pathname + '#/?id=' + dossierNo + '&secretKey=' + secretKey;
        window.location.href = url;
      } // 
      // let searchParams = window.location.search.replace('?','')
      // if (searchParams) {
      //   let params = vm.parse_query_string(searchParams)
      //   if (params.hasOwnProperty('id') && params.id && params.hasOwnProperty('secretKey') && params.secretKey) {
      //     let url = window.location.origin + window.location.pathname + '#/?id=' + params.id + '&secretKey=' + params.secretKey
      //     window.location.href = url
      //   }
      // }
      // 

    });
  },
  beforeDestroy: function beforeDestroy() {
    if (typeof window !== 'undefined') {
      window.removeEventListener('resize', this.onResize, {
        passive: true
      });
    }
  },
  mounted: function mounted() {
    this.onResize();
    window.addEventListener('resize', this.onResize, {
      passive: true
    });
  },
  watch: {
    '$route': function $route(newRoute, oldRoute) {
      var vm = this;
      var currentParams = newRoute.params;
      var currentQuery = newRoute.query;
    }
  },
  methods: {
    onResize: function onResize() {
      var vm = this;
      vm.isMobile = window.innerWidth < 1024;
    },
    parse_query_string: function parse_query_string(query) {
      var vars = query.split('&');
      var query_string = {};

      for (var i = 0; i < vars.length; i++) {
        var pair = vars[i].split('=');
        var key = decodeURIComponent(pair[0]);
        var value = decodeURIComponent(pair[1]);

        if (typeof query_string[key] === 'undefined') {
          query_string[key] = decodeURIComponent(value);
        } else if (typeof query_string[key] === 'string') {
          var arr = [query_string[key], decodeURIComponent(value)];
          query_string[key] = arr;
        } else {
          query_string[key].push(decodeURIComponent(value));
        }
      }

      return query_string;
    }
  }
};